div { margin: 0;}


.Home-section {
  font-size: large;
  background-color:#dae2e8;
  padding-left: 10%;
  padding-right: 10%;
  padding-top: 2%;
  padding-bottom: 1%;
}

.Sun {
  padding: 2%;
  height: 15%;
  width: 10%;
}

.Welcome {
  font-family: palatino;
  color: #485167;
  padding: 1%;
  padding-bottom: 3%;
  font-size: medium;
  margin: 0%
}

.Clouds {
  height: 10vh;
}

.Buildings {
  margin-top: 6%;
  height: 20%;
  width: 80%;
}


.Line {
  color: #485167;
}

/* KEYFRAMES */

@-webkit-keyframes animateCloud {
    0% {
        margin-left: -400px;
    }
    100% {
        margin-left: 100%;
    }
}

@keyframes animateCloud {
    0% {
        margin-left: -400px;
    }
    100% {
        margin-left: 100%;
    }
}

/* ANIMATIONS */

.x1 {
	-webkit-animation: animateCloud 35s linear infinite;
	animation: animateCloud 35s linear infinite;

	-webkit-transform: scale(0.15);
	-ms-transform: scale(0.15);
	    transform: scale(0.15);
  position: absolute;
  padding-top: 3%;
}

.x2 {
	-webkit-animation: animateCloud 40s linear infinite;
	animation: animateCloud 40s linear infinite;

	-webkit-transform: scale(0.1);
	-ms-transform: scale(0.1);
	    transform: scale(0.1);
  position: absolute;
}

.x3 {
	-webkit-animation: animateCloud 33s linear infinite;
	animation: animateCloud 33s linear infinite;

	-webkit-transform: scale(0.2);
	-ms-transform: scale(0.2);
	    transform: scale(0.2);
  position: absolute;
  padding-bottom: 5%;
  padding-right: 25%;
}


.x4 {
	-webkit-animation: animateCloud 50s linear infinite;
	animation: animateCloud 50s linear infinite;

	-webkit-transform: scale(0.125);
	-ms-transform: scale(0.125);
	    transform: scale(0.125);
  position: absolute;
  padding-top: 3%;
}

.x5 {
	-webkit-animation: animateCloud 45s linear infinite;
	animation: animateCloud 45s linear infinite;

	-webkit-transform: scale(0.175);
	-ms-transform: scale(0.175);
	    transform: scale(0.175);
  position: absolute;
  padding-top: 8%;
  padding-right: 15%;
}

/* OBJECTS */

.cloud {
	background: #fff;
	background: -webkit-gradient(linear, left top, left bottom, color-stop(5%,#fff), color-stop(100%,#f1f1f1));
	background: -webkit-linear-gradient(top,  #fff 5%,#f1f1f1 100%);
	background: -o-linear-gradient(top,  #fff 5%,#f1f1f1 100%);
	background: -ms-linear-gradient(top,  #fff 5%,#f1f1f1 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fff', endColorstr='#f1f1f1',GradientType=0 );
	border-radius: 100px;

	-webkit-box-shadow: 0 8px 5px rgba(0, 0, 0, 0.1);
	box-shadow: 0 8px 5px rgba(0, 0, 0, 0.1);

	height: 100px;
	position: relative;
	width: 330px;
}

.cloud:after, .cloud:before {
  background: #fff;
	content: '';
	position: absolute;
	z-indeX: -1;
}

.cloud:after {
	border-radius: 100px;

	height: 80px;
	left: 50px;
	top: -50px;
	width: 80px;
}

.cloud:before {
	border-radius: 200px;

	width: 160px;
	height: 160px;
	right: 50px;
	top: -90px;
}



