.typewriter h1 {
  font-family: palatino;
  color: #485167;
  overflow: hidden;
  border-right: .10em solid transparent;
  white-space: nowrap;
  margin: 0 auto;
  -webkit-animation:
    typing 4s steps(30, end),
    blinking-cursor .5s step-end,
    fadeOut 3s;
          animation:
    typing 4s steps(30, end),
    blinking-cursor .5s step-end,
    fadeOut 3s;
}

/*Typing effect */
@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

/* Typewriter cursor effect */
@-webkit-keyframes blinking-cursor {
  0% { border-color: transparent }
  50% { border-color: #485167; }
}
@keyframes blinking-cursor {
  0% { border-color: transparent }
  50% { border-color: #485167; }
}

@-webkit-keyframes fadeOut {
  0% { border-color: #485167 }
  80% { border-color: transparent }
}

@keyframes fadeOut {
  0% { border-color: #485167 }
  80% { border-color: transparent }
}



